<template>
    <div class="el-card-style" @mouseenter="mouseover()" @mouseleave="mouseout()">
      <div class="img-contain">
        <transition name="action-fade">
          <div class="action-circle" v-show="flag"></div>
        </transition>
        <!-- :src="cardVo.image" v-lazy="cardVo.image" -->
        <img v-lazy="cardVo.image" alt="图片数据丢失">
        <div class="tag"></div>
      </div>
      <div style="padding: 14px;">
        <span>好吃的汉堡</span>
        <div>
          <time>{{ new Date() }}</time><br>
          introduction <br>
          The Modern Element However players can deactivate this at any time. <br>
          Players can even travel across planets if they wish to enter a different environment. <br>
          Phone Apps customers can access their ... read more <br>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    cardVo: Object
  },
  data () {
    return {
      flag: false
    }
  },
  mounted () {
    console.log('Card mounted loading')
  },
  methods: {
    mouseover () {
      // document.getElementsByClassName('action-circle')[1].style.display = 'block'
      this.flag = true
      // console.log('123')
    },
    mouseout () {
      // document.getElementsByClassName('action-circle')[1].style.display = 'none'
      this.flag = false
      // console.log('456')
    }
  }
}
</script>

<style scoped>
::selection{
  background-color: #01b18e;
  color: aliceblue;
}
.el-card-style{
  /* border:1px dashed #00a1ba; */
  padding: 0;
  height: 100%;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 2px 2px rgba(18,18,19,.5), 0px -1px 1px rgba(18,18,19,.2);
  border-radius: 7px;
  transition: all .3s ease 0s;
}
.img-contain{
  /* border: 1px dashed #485DA6; */
  position: relative;
  height: 220px;
  overflow: hidden;
  text-align: center;
  border-radius: 7px 7px 0px 0px;
  background: linear-gradient(to right bottom, #6d327c, #485DA6, #00a1ba, #01b18e, #32b37b);
}
img{
  width: 100%;
  height: 220px;
  object-fit: cover;
  transition: all 0.5s;
}
.tag{
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 30px;
  bottom: 0;
  background: rgba(18, 18, 19, .5);
  /* background: rgba(1, 177, 142, .5); */
}
.action-circle{
  position: absolute;
  z-index: 1;
  width: 70%;
  height: 80px;
  border-radius: 35px;
  left: 15%;
  margin-top: -40px;
  /* background: #333; */
  background: rgba(18, 18, 19, .9);
  /* background: rgba(1, 177, 142, .5); */
  /* transition: all .7s; */
}
.action-fade-enter, .action-fade-leave-to{
  transform: translateY(-40px);
  /* opacity: 0.1; */
}
.action-fade-enter-active, .action-fade-leave-active{
  transition: all .3s ease 0s;
}
img:hover{
  transform: scale(1.1,1.1);
  opacity: 0.8;
}
.el-card-style:hover{
  transform: translateY(-5px);
  box-shadow: 4px 4px 5px rgba(18,18,19,.3),-4px 0px 5px rgba(18,18,19,.3);
}
</style>



